import React from 'react'
import Footer from './footer'
import Img from "./img/about_hero.png"
import { BlogCard1, BlogCard2, BlogCard2inCard1, BlogCard2inCard1flex, BlogCard2inCard2, BlogCard2inCard3, BlogCard2inCard3flex, BlogFlex, BlogImg, BlogInFlex, BlogText, BlogWrap, Card4, Card5flex, Page,ContactCont, ContactFlex, ConatctInputs } from './styled'

function Contact() {
    return (
        <div>
            <BlogWrap style={{ backgroundImage: `url(${Img})` }}>
                <h1>Contacts</h1>
            </BlogWrap>
            <br /><br /><br />
            <ContactCont>
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d52214.445632089795!2d70.06856450268464!3d41.01252667541779!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x38afeeb0f032acbf%3A0x6af0123d5787be09!2z0JDQvdCz0YDQtdC9LCDQo9C30LHQtdC60LjRgdGC0LDQvQ!5e0!3m2!1sru!2s!4v1633167476185!5m2!1sru!2s" style={{ width: "100%", height: "450px" }} allowfullscreen="" loading="lazy"></iframe>
                <br /><br /><br /><br />
                <ContactFlex>
                    <ConatctInputs>
                        <h3>Get in Touch</h3>
                        <br />
                        <textarea></textarea>
                        <br /><br />
                        <div>
                            <input type="text" placeholder="name"/>
                            <input type="text" placeholder="email"/>
                        </div>
                        <br />
                        <input type="text" className="input" placeholder="enter subject" />
                        <br /><br />
                        <button>S E N D</button>
                    </ConatctInputs>
                </ContactFlex>
                <br /><br /><br /><br /><br /><br /><br /><br />
                <Footer/>
            </ContactCont>

        </div>
    )
}

export default Contact
