import React from 'react'
import Footer from './footer'
import Img from "./img/about_hero.png"
import { ButtonFlex, CartCont, CartFlex, Flex, LoginWrap } from './styled'
function Cart() {
    return (
        <>
            <LoginWrap style={{ backgroundImage: `url(${Img})` }}>
                <h1>Cart</h1>
            </LoginWrap>
            <CartCont>
                <CartFlex>
                    <div className="p">prouct</div>
                    <div className="div">
                        <div className="price">price</div>
                        <div className="q">quantity</div>
                        <div className="t">total</div>
                    </div>
                </CartFlex>
                <div className="bb" />
                <br />
                <Flex>
                    <div className="watch">
                        <img src="https://tmb-01.github.io/timezone/assets/img/gallery/card1.png" alt="" />
                        <p>Minimalistic shop for multipurpose use</p>
                    </div>
                    <div className="price">
                        <div className="p">$360.00</div>
                        <div className="counter"><div className="one">1</div><div className="place"><div className="plus">+</div><div className="minus">-</div></div></div>
                        <div className="all1">$720.00</div>
                    </div>
                </Flex>
                <br />
                <div className="bb" />
                <br />
                <Flex>
                    <div className="watch">
                        <img src="https://tmb-01.github.io/timezone/assets/img/gallery/card2.png" alt="" />
                        <p>Minimalistic shop for multipurpose use</p>
                    </div>
                    <div className="price">
                        <div className="p">$360.00</div>
                        <div className="counter"><div className="one">1</div><div className="place"><div className="plus">+</div><div className="minus">-</div></div></div>
                        <div className="all1">$720.00</div>
                    </div>
                </Flex>
                <br />
                <div className="bb"></div>
                <br />
                <ButtonFlex>
                    <button>Update Card</button>
                    <button>Close Coupon</button>
                </ButtonFlex>
                <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
               <Footer/>
                
            </CartCont>
        </>
    )
}

export default Cart
